import styled, { keyframes } from 'styled-components';

const fadeIn = keyframes`
    0% {opacity: 0}
    100% {opacity: 1}
`;

export const Wrapper = styled.div`
        animation: ${fadeIn} ${props => props.delay ? '1s' : '3s'};
        filter: blur(0px) brightness(150%);
        position: absolute;
        overflow: hidden;
        height: 100%;
        width: 100%;
        z-index: -1;
        left: 0;
        top: 0;
`;